.footer-container {
  display: flex;
  justify-content: center;
  gap: 50px;
  width: 100%;
  margin-bottom: 1.5rem;
  list-style-type: none;
  margin-top: 3rem;
}

.footer-links {
  text-decoration: none;
  color: #1b1b1b;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 15px;
  letter-spacing: 1px;
  transition: all 0.9s ease;
}
.footer-links:hover {
  text-decoration: overline;
}
