.reminder-container {
  text-align: center;
  color: #1b1b1b;
  margin: 3rem;
  line-height: 1.88rem;
}
.reminder-container h3 {
  font-size: 2.5rem;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}
.tel {
  text-decoration: none;
  color: #1b1b1b;
  transition: all 0.8s ease;
}
.tel:hover {
  color: grey;
  text-decoration: overline;
}
