.NavbarItems {
  background: #1b1b1b;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.NavbarItems ul {
  display: flex;
  justify-content: center;
  gap: 100px;
  width: 100%;
  margin-bottom: 1.5rem;
}

.NavbarItems ul li {
  list-style-type: none;
}
.nav-links {
  text-decoration: none;
  color: #fafafa;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 1px;
  transition: all 0.9s ease;
}
.nav-links:hover {
  text-decoration: overline;
}
.menu-logo {
  background: #1b1b1b;
  display: flex;
  justify-content: center;
}

.logo {
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
  width: 160px;
}

.menu-icon {
  align-self: start;
  position: absolute;
  top: 2%;
  left: 2%;
  color: rgba(250, 250, 250, 0.5);
  cursor: pointer;
  transition: all 0.8s ease;
  display: none;
}
.menu-icon:hover {
  color: rgba(250, 250, 250, 0.8);
}

.hamburger {
  font-size: 1.8rem;
}
@media screen and (max-width: 600px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #6668f4;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #7577fa;
    border-radius: 0;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: -50px;
    z-index: 99;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #4ad9e4;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #6568f4;
    transition: 250ms;
  }

  Button {
    display: none;
  }
}
