* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Yantramanav", sans-serif;
}
body {
  background-color: #e7e7e7;
}
.banner-container {
  background-image: url("https://media.gucci.com/content/DiaryArticleDouble_Standard_1400x894/1611238504/DiaryArticleDouble_S11-WinterinthePark-Beloved-04_001_Default.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  height: 100vh;
  margin: 3.2rem 1.3rem;
}
.banner-container .content {
  position: absolute;
  top: 50%;
  left: 4%;
  color: #fafafa;
}
.campaign-title {
  font-size: 2.8rem;
  font-weight: 300;
  letter-spacing: 1px;
}
.campaign-link {
  font-style: italic;
  color: #fafafa;
  font-size: 1.8rem;
  text-decoration: none;
  font-weight: 300;
}
.campaign-link:hover {
  text-decoration: overline;
}

.links-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  gap: 50px;
  margin-top: 3rem;
}
.icon-link a {
  color: #1b1b1b;
  font-size: 1.3rem;
}
.line {
  margin: 0 auto;
  height: 1px;
  width: 50px;
  background-color: #1b1b1b;
  margin-top: 3rem;
}
.disclaimer {
  text-align: center;
  margin-top: 3rem;
  color: grey;
}
.btn {
  cursor: pointer;
}
.privacy-container {
  text-align: center;
  background-color: #c4c4c4;
  padding: 1.5rem;
  margin-top: 3rem;
  line-height: 1.88;
}
.privacy-container a {
  color: #1b1b1b;
}
.App {
  margin: 4rem;
}
@media screen and (max-width: 600px) {
  .App {
    margin: 0;
  }
  .banner-container {
    margin: 3.2rem 0rem;
  }
  .campaign-title {
    font-size: 1.8rem;
  }
  .campaign-link {
    font-size: 1.1rem;
  }
  .footer-container {
    display: flex;
    flex-direction: column;
    margin-left: 2%;
  }
}
